.App {
  width: 50%;
  margin: 0 auto;
  text-align: center;
}

body {
  font-family: "Courgette", cursive;
  background: #ffe5d9;
  color: #9d8189;
}

#filter {
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;
}

#filter-button {
  width: 30%;
  padding: 10px;
  margin-left: 15px;
  margin-right: 15px;
  cursor: pointer;
  outline: none;
  border: none;
  background: transparent;
  font-size: 20px;
  font-family: "Courgette", cursive;
  color: #9d8189;
  font-weight: bold;
}

#filter-button.active {
  border-bottom: 5px solid #ffcad4;
}

#new-todo {
  width: 40%;
  padding: 10px;
  margin-right: 10px;
  font-size: 20px;
  font-family: "Courgette", cursive;
  background: transparent;
  border: 3px solid #ffcad4;
  border-radius: 10px;
  color: #9d8189;
  outline: none;
  box-shadow: 0 0 10px #ffcad4;
}

#new-todo::placeholder {
  color: #9d8189;
}

#add-todo {
  padding: 10px;
  cursor: pointer;
  font-size: 20px;
  font-family: "Courgette", cursive;
  background: transparent;
  border: 3px solid #d8e2dc;
  border-radius: 10px;
  color: #9d8189;
  outline: none;
}

#add-todo:hover {
  box-shadow: 0 0 5px #d8e2dc;
}

#clear {
  padding: 5px;
  margin-left: 10px;
  cursor: pointer;
  font-size: 20px;
  font-family: "Courgette", cursive;
  background: transparent;
  border: 3px solid #d8e2dc;
  border-radius: 10px;
  color: #f4acb7;
  outline: none;
}

#clear:hover {
  box-shadow: 0 0 10px #d8e2dc;
}

#list {
  margin-top: 24px;
  list-style: square;
  text-align: left;
}

#list-item {
  padding: 15px;
  font-size: 22px;
}

li.completed {
  text-decoration: line-through;
}
